"use client"

import { useState, useEffect, type ReactNode } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { Button } from "./ui/button"
import { Input } from "./ui/input"
import { Label } from "./ui/label"
import { Card } from "./ui/card"
import { MapPin, User } from "lucide-react"
import { useUser } from "@/store/user-provider"

const CITIES = [
  { name: "–ê–ª–º–∞—Ç—ã", coords: [43.238293, 76.889709] as [number, number] },
  { name: "–ê—Å—Ç–∞–Ω–∞", coords: [51.169392, 71.449074] as [number, number] },
  { name: "–ü–∞–≤–ª–æ–¥–∞—Ä", coords: [52.287054, 76.967155] as [number, number] },
]

interface OnboardingFlowProps {
  children: ReactNode
}

export default function OnboardingFlow({ children }: OnboardingFlowProps) {
  const { user, setUser } = useUser()
  const [step, setStep] = useState<"city" | "iin" | "complete">("city")
  const [selectedCity, setSelectedCity] = useState("")
  const [iin, setIin] = useState("")
  const [error, setError] = useState("")

  useEffect(() => {
    if (user) {
      setStep("complete")
    }
  }, [user])

  const handleCitySelect = (cityName: string) => {
    setSelectedCity(cityName)
  }

  const handleContinueFromCity = () => {
    if (!selectedCity) {
      setError("–í—ã–±–µ—Ä–∏—Ç–µ –≥–æ—Ä–æ–¥")
      return
    }
    setError("")
    setStep("iin")
  }

  const handleIINSubmit = () => {
    if (iin.length !== 12 || !/^\d+$/.test(iin)) {
      setError("–ò–ò–ù –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å –∏–∑ 12 —Ü–∏—Ñ—Ä")
      return
    }

    setError("")
    const cityData = CITIES.find((c) => c.name === selectedCity)
    setUser({
      iin,
      city: selectedCity,
      cityCoords: cityData?.coords || CITIES[0].coords,
    })
    setStep("complete")
  }

  if (step === "complete") {
    return <>{children}</>
  }

  return (
    <div className="fixed inset-0 z-[9999] flex items-center justify-center bg-background">
      <AnimatePresence mode="wait">
        {step === "city" && (
          <motion.div
            key="city"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="w-full max-w-md px-4"
          >
            <Card className="p-8 border">
              <div className="flex flex-col items-center gap-6">
                <div className="p-3 bg-primary/10 rounded-lg">
                  <MapPin className="h-8 w-8 text-primary" />
                </div>
                <div className="text-center space-y-2">
                  <h1 className="text-2xl font-semibold">–í—ã–±–µ—Ä–∏—Ç–µ –≥–æ—Ä–æ–¥</h1>
                  <p className="text-muted-foreground">–°–∏—Å—Ç–µ–º–∞ –ø–æ–¥–∞—á–∏ –∑–∞—è–≤–æ–∫ –¥–ª—è –ö–°–ö</p>
                </div>

                <div className="w-full space-y-3">
                  {CITIES.map((city) => (
                    <Button
                      key={city.name}
                      variant={selectedCity === city.name ? "default" : "outline"}
                      className="w-full h-14 text-lg"
                      onClick={() => handleCitySelect(city.name)}
                    >
                      {city.name}
                    </Button>
                  ))}
                </div>

                {error && <p className="text-sm text-destructive">{error}</p>}

                <Button onClick={handleContinueFromCity} className="w-full" size="lg">
                  –ü—Ä–æ–¥–æ–ª–∂–∏—Ç—å
                </Button>
              </div>
            </Card>
          </motion.div>
        )}

        {step === "iin" && (
          <motion.div
            key="iin"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="w-full max-w-md px-4"
          >
            <Card className="p-8 border">
              <div className="flex flex-col items-center gap-6">
                <div className="p-3 bg-primary/10 rounded-lg">
                  <User className="h-8 w-8 text-primary" />
                </div>
                <div className="text-center space-y-2">
                  <h1 className="text-2xl font-semibold">–í–≤–µ–¥–∏—Ç–µ –ò–ò–ù</h1>
                  <p className="text-muted-foreground">–î–ª—è –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏–∏ –ø—Ä–∏ –ø–æ–¥–∞—á–µ –∑–∞—è–≤–æ–∫</p>
                </div>

                <div className="w-full space-y-2">
                  <Label htmlFor="iin">–ò–ò–ù (12 —Ü–∏—Ñ—Ä)</Label>
                  <Input
                    id="iin"
                    type="text"
                    placeholder="000000000000"
                    value={iin}
                    onChange={(e) => setIin(e.target.value.replace(/\D/g, "").slice(0, 12))}
                    maxLength={12}
                    className="text-center text-lg tracking-wider h-14"
                  />
                </div>

                {error && <p className="text-sm text-destructive">{error}</p>}

                <div className="w-full flex gap-3">
                  <Button variant="outline" onClick={() => setStep("city")} className="flex-1">
                    –ù–∞–∑–∞–¥
                  </Button>
                  <Button onClick={handleIINSubmit} className="flex-1">
                    –í–æ–π—Ç–∏
                  </Button>
                </div>
              </div>
            </Card>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  )
}
